// ___________Gallery grid_______________-

.gallery {
  width: 100%;
  display: grid;
  grid-gap: 16px;

  @media screen and (min-width: $widthTablet) {
    grid-template-columns: repeat(auto-fit, minmax(335px, 1fr));
    grid-gap: 32px 16px;
  }

  @media screen and (min-width: $widthDesktop) {
    grid-template-columns: repeat(auto-fit, minmax(394px, 1fr));
  }
}

