// ____________gallery card_________________

.gallery-card {
  display: flex;
  justify-content: center;
  // background-color: aqua;
}

.gallery_link {
  transition: transform 400ms easy-out;
  &:hover {
    transform: scale(1.03);
  }

  max-width: 280px;

  @media screen and (min-width: $widthTablet) {
    max-width: 335px;
  }
  @media screen and (min-width: ($widthDesktop)) {
    max-width: 394px;
  }
}

.movie_img {
  box-shadow: $shadow;
  border-radius: 10px;
  width: 100%;
  height: 398px;
  object-fit: cover;

  @media screen and (min-width: $widthTablet) {
    width: 336px;
    height: 455px;
  }
  @media screen and (min-width: ($widthDesktop)) {
    width: 394px;
    height: 574px;
  }
}

.wrapper {
  position: relative;
}

.movie_title.card {
  font-size: 16px;
  line-height: 20px;
  max-width: 280px;
  @media screen and (min-width: $widthTablet) {
    font-size: 20px;
    line-height: 24px;
  }
}

.movie_describtion {
  display: flex;
  font-size: 16px;
  line-height: 20px;
  color: $accentColor;
  align-items: baseline;
  @media screen and (min-width: $widthTablet) {
    font-size: 20px;
    line-height: 24px;
  }
}

.movie_genresList {
  position: relative;
  display: flex;
  margin-right: 16px;

  &::after {
    content: '';

    position: absolute;
    display: block;
    top: 0;
    left: calc(100% + 8px);

    height: 100%;
    width: 2px;
    background-color: $accentColor;
  }
}

.movie_average {
  font-size: 18px;
  margin-left: 8px;
  background-color: $accentColor;
  padding: 2px 8px;
  color: #fff;
  border-radius: 4px;
}
